* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #0b2434;
  padding: 20px;
}

main {
  background: #F5F5F5;
  height: 400px;
  max-width: 800px;
  border-radius: 0.25rem;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.title {
  font-size: 40px;
}

.instructions {
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}

.die-num {
  font-size: 2rem;
}

.roll-dice {
  height: 50px;
  width: 100px;
  border: none;
  border-radius: 0.25rem;
  background: #5035ff;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}